import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../Services/Utils/utils';

class ConntectPagesModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    handleClose = () => {
        this.props.modalCallback(false)
    };

    render() {
        const { modalToggle, childConnectionList } = this.props

        return <Modal show={modalToggle} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Social Pages</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="connection_modal mx-xl-4 px-2 px-lg-4 mx-0 overflow-auto">
                    {
                        childConnectionList.map((item) => {
                            return <div className="d-flex justify-content-between align-items-center py-3 border-bottom">
                                <div className="d-flex align-items-center flex-grow-1 me-2 w-75">
                                    <div className="symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden bg-light">
                                        <img className="object-center-cover" src={item.ConnectedAccounts.profilePicture} height={40} width={40} alt="" />
                                    </div>
                                    <div className="overflow-hidden">
                                        <p className="text-gray-800 mb-0 w-100 fw-bold text-ellipsis">{item.ConnectedAccounts.value2}</p>
                                        <p className="mb-0 text-gray-600 text-ellipsis mb-0 fs-8">
                                            @{item.ConnectedAccounts.value1}
                                        </p>
                                    </div>
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0">
                                    <div className="fs-7 fw-bold text-gray-800"> <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.ConnectedAccounts.networkId)}.svg`)} height={28} width={28} alt="" /></div>
                                    <div className="fs-8 text-muted fw-medium d-none">Posts</div>
                                </div>
                            </div>
                        })
                    }
                </div>
            </Modal.Body>
            {/* <Modal.Footer>
                        <button type="button" className="btn btn-primary btn-sm" onClick={this.handleClose}>Add Account</button>
                    </Modal.Footer> */}
        </Modal>

    }
}

export default ConntectPagesModal;